.back
{
    position: absolute;
    right: 8%;
    top:-70px;
}

.single-page
{
    position: relative;
}

.main-content p
{
    margin: 10px 0 20px 0;
    color: grey;
}

.main-content img
{
    width: 100%;
    height: auto;
}

.main-content .para p
{
    margin-right: 10px;
}

.main-content h1
{
    font-size: 30px;
}

article .box
{
    background-color: #31b675;
    padding: 30px;
    color: #fff;
}
article .box .outline-btn
{
    font-size: 15px;
    color: #fff;
}

article .box i
{
    color:#fff;
}

article .box h2
{
    font-size: 30px;
    font-weight: 500;
}

article .box2
{
    margin-top: 40px;
    background: url("../../public/image/side-bg.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    height: 180px;
    width: 100%;
    position: relative;
    padding: 25px;
}

article .box2::after
{
    content: "";
    position: absolute;
    top:0;
    left: 0;
    background-color: rgba(0,0,0,0.5);
    height: 180px;
    width: 100%;
}

article p
{
    font-size: 14px;
    line-height: 25px;
}

article .box2 p
{
    text-align: center;
    position: relative;
    z-index: 2;
    font-size: 14px;
    line-height: 25px;
    color: #fff;
}

@media screen and (max-width:768px)
{
    .single-page .flex
    {
        flex-direction: column;
    }
}