.gallery {
    margin-bottom: 50px;
}
.gallery-container
{
    padding-left: 30px;
    padding-right: 30px;
}

.gallery img {
    transition: 0.5s;
    width: 100%;
    height: 100%;
}

.gallery .items {
    padding: 15px;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    text-align: center;
}
.gallery h3 {
    margin: 10px 0 10px 0;
    font-weight: 400;
}
  .gallery .img {
    position: relative;
    overflow: hidden;
    width: 380px;
    height: 252px;
}

.gallery i {
    position: absolute;
    top: 45%;
    left: 45%;
    z-index: 5;
    font-size: 40px;
    color: white;
    opacity: 0;
    transition: 0.5s ease-in-out;
}

.gallery .img::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 380px;
    height: 252px;
    z-index: 2;
    background-color: rgba(0, 0, 0, 0.5);
    opacity: 0;
    transition: 0.5s ease-in-out;
}

.gallery .img::after {
    content: "";
    position: absolute;
    top: 10px;
    left: 10px;
    width: 360px;
    height: 232px;
    z-index: 3;
    opacity: 0;
    transition: 0.5s ease-in-out;
}

.gallery .items:hover img {
    transform: scale(1.2);
    cursor: pointer;
}

.gallery .items:hover i,
.gallery .items:hover .img::after,
.gallery .items:hover .img::before {
  opacity: 1;
  cursor: pointer;
}

.popup {
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    position: fixed;
    z-index: 8;

}

.hide {
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.7);
}


.popup-content {
    position: absolute;
    top: 15%;
    left: 20%;
    bottom: 5%;
}


.popup-content button {
    background-color: #fff;
    padding: 10px;
    color: #000;
    position: absolute;
    top: -5%;
    right: -20%;
    font-weight: bold;
}

@media screen and (max-width: 768px) {
    .gallery .container {
      max-width: 100%;
    }

    .gallery .img {
        width: 100%;
        height: 200px;
    }
    

     .gallery .img::before {
        width: 100%;
        height: 100%;
        opacity: 0;
     }
    .popup-content {
        padding: 50px;
        position: absolute;
        top: 30%;
        left: 0%;
      }
    .popup button {
        background-color: #fff;
        padding: 10px;
        color: #000;
        position: absolute;
        top: -10%;
        right: 6.5%;
        font-weight: bold;
      }
}